        handlePreviewPanel(panel) {
            const row = this.panelListIndexed[panel.panelId]
            const version = row.duplicateVersion || row.currentVersion
            const url = this.basicFn.numToPannelTypeUrl(row.pannelType) + '/preview.html?id=' + row.pannelGroupId + '&version=' + version;
            this.FastDevTool.createDialogWin('edit-view', {
                confirmInfo: false,
                fit: true,
                iconCls: 'icon-edit',
                minimizable: false,
                maximizable: true,
                title: '',
