                onClose: function() {
                    this.updatePanelVersion(row, function() {
                        this.loadPanelDetail(row)
                    }.bind(this))
                }.bind(this)
            })
        },
        handleClickBlock(data) {
            const index = data.index
            const crowdIndex = data.crowdIndex
            const activeIndex = data.activeIndex
            const blockIndex = data.blockIndex
            const panel = this.getPanelByIndex(index, crowdIndex)
            const panelData = this.panelListIndexed[panel.id]
            const panelStatus = panelData.pannelStatus
            const STATUS = this.STATUS

            const version = panelData.duplicateVersion || panelData.currentVersion
            const isJiangSu = $idPrefix === "11" ? true : false
            let url
            switch(true) {
                case isJiangSu && panelData.pannelGroupId.toString().indexOf(10) === 0:
                    // (10)
                    url = this.basicFn.numToPannelTypeUrl(panelData.pannelType) + '/preview.html?id=' + panelData.pannelGroupId + '&version=' + version;
                    break;
                case panelStatus ===  STATUS.draft :
                    // 
                    url = this.basicFn.numToPannelTypeUrl(panelData.pannelType) + '/edit.html?id=' + panelData.pannelGroupId + '&version=' + version;
                    url += "&clickActiveIndex=" + activeIndex + "&clickBlockIndex=" + blockIndex
                    break;
                case panelStatus === STATUS.accepted:
                   // 
                    url = this.basicFn.numToPannelTypeUrl(panelData.pannelType) + '/editHistory.html?id=' + panelData.pannelGroupId + '&version=' + version;
                    url += "&clickActiveIndex=" + activeIndex + "&clickBlockIndex=" + blockIndex
                    break;
                case panelStatus === STATUS.waiting:
                    url = this.basicFn.numToPannelTypeUrl(panelData.pannelType) + '/preview.html?id=' + panelData.pannelGroupId + '&version=' + version;
                    break;
            }
            this.FastDevTool.createDialogWin('edit-view', {
                confirmInfo: false,
                fit: true,
                iconCls: 'icon-edit',
                minimizable: false,
                maximizable: true,
                title: '',
                content: this.FastDevTool.createIframe(url),
                onClose: function() {
                    this.updatePanelVersion(panelData, function() {
                        this.loadPanelDetail(panelData)
                    }.bind(this))
                }.bind(this)
            })
        },
