/**  */
// list
export function getMarkPanelList (data) {
  return this.fetch({
    method: 'post',
    url: 'api/panel/pageList.html',
    data
  })
}
// 
export function getMarkEditData (params) {
  return this.fetch({
    method: 'get',
    url: 'api/markPanel/edit.html',
    params
  }).then(data => {
    return JSON.parse(data.match(/pannelInit = (\{.+\})/)[1])
  })
}
